.header
{
  background-color: skyblue;
  text-align: center;
}

.product
{
  background-color: bisque;
  width: fit-content;
  padding: 20px;
  margin: 10px;
  text-align: center;
}

.product:hover
{
  transform: scale(1.3);
  margin: 50px;
  /*transition-delay: 5s;*/
}

.products
{
  display: flex;
  background-color: aqua;
}

.img
{
  width: 200px;
}

.btn
{
  background-color: blueviolet;
  padding: 15px;
  margin: 10px;
  font-size: 25px;
  color: antiquewhite;
  border-radius: 5px;
}

.btn:hover
{
  cursor: pointer;
  background-color: antiquewhite;
  color: blueviolet;
  transition: 1.5s;
}